@import "bootstrap/scss/mixins";
@import "bootstrap/scss/functions";
@import "mixins";
@import "colors";
@import "variables";

@import "components/typography";

@import "views/header";
@import "views/hero";
@import "views/showreel";
@import "views/section";
@import "views/form";
@import "views/footer";

html,
body {
	width: 100%;
	height: 100%;
	padding: 0;
	margin: 0;
	color: #000;
}

body {
	-webkit-overflow-scrolling: touch;

	@include sm-down {
		background: blue;
	}
}

.page {
	min-height: 100vh;
	margin: 0;
	padding: 0;
}

.wrapper {
	min-height: 100vh;
	margin: 0 auto;
	padding: 0;
}

.section {
	position: relative;
}